	module.exports = {"data":{"markdownRemark":{"html":"<p>HTML elements are essentially little rectangles. Three important attributes control the space that surrounds each HTML element: <code>padding</code>, <code>margin</code>, and <code>border</code>. An element’s padding controls the amount of space between the element and its border.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token selector\">.green-box</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> green<span class=\"token punctuation\">;</span>\n      <span class=\"token property\">padding</span><span class=\"token punctuation\">:</span> 20px<span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>","fields":{"slug":"/certificates/adjust-the-padding-of-an-element/"},"frontmatter":{"title":"Adjust the Padding of an Element"}}},"pathContext":{"slug":"/certificates/adjust-the-padding-of-an-element/"}}

